









  import { Component, Vue } from "vue-property-decorator";
  import LocalForage from "localforage";
@Component
export default class home extends Vue {
  async drC() {
    if (confirm("从网络导入？")) {
      let res = await (await fetch("data.json")).json();
      for (let item of Object.entries(res)) {
        LocalForage.setItem(item[0], item[1]);
      }
      alert("成功")
    } else {
      let ele = document.createElement("input");
      ele.style.display = "none";
      ele.type = "file";
      ele.addEventListener("change", () => {
        let reader = new FileReader();
        reader.onload = () => {
          let fileString = reader.result;
          if (fileString) {
            let res = JSON.parse(fileString.toString());
            for (let item of Object.entries(res)) {
              LocalForage.setItem(item[0], item[1]);
            }
            alert("成功")
          }
          ele.remove();
        };
        if (ele.files) reader.readAsText(ele.files[0], "UTF-8");
      });
      ele.click();
    }
  }
  async scC() {
    let o: any = {};
    for (let key of await LocalForage.keys()) {
      o[key] = await LocalForage.getItem(key);
    }
    let os = new Blob([JSON.stringify(o)], { type: "text/plain" });
    let ele = document.createElement("a");
    ele.download = "data.json";
    ele.style.display = "none";
    ele.href = URL.createObjectURL(os);
    document.body.appendChild(ele);
    ele.click();
    document.body.removeChild(ele);
  }
}
