import { Module, VuexModule, Mutation, Action } from 'vuex-module-decorators'
import {SzDT} from "@/store/szDT";
import LocalForage from "localforage";

interface dataObject {
    [key: string]: SzDT[]
}
@Module
export default class data extends VuexModule {
    data:dataObject={};
    @Mutation
    setData(pay: { key: any; data: any; }) {
        let {key,data} = pay;
        LocalForage.setItem(key, data);
        this.data[key] = data
    }
    @Action({commit: 'setData'})
    async oneData(key: string) {
        let data: any[];
        if (this.data.hasOwnProperty(key)) {
            data= this.data[key]
        } else {
            data = (await LocalForage.getItem(key)) as SzDT[];

        }
        return {key,data}
    }
    // @ts-ignore
    @Action
    oneFjData(pay: { key: any; fj: any; }) {
        let {key,fj} = pay;
        return this.data[key].find(e => e.fjh === fj)
    }
}
