import { Module, VuexModule, Mutation, Action } from 'vuex-module-decorators'
import {SzDT} from "@/store/szDT";
import LocalForage from "localforage";

export const ts = Symbol();
@Module
export default class sz extends VuexModule {
    sz:SzDT[]=[];
    get szz() {
        return this.sz
    }
    @Mutation
    setSz(szs: SzDT[]) {
        LocalForage.setItem("sz", szs);
        this.sz = szs
    }
    @Action({commit: 'setSz'})
    async getSz() {
        return (await LocalForage.getItem("sz")) as SzDT[]
    }
    @Action
    oneFjSz(fj:string) {
        return this.sz.find(e => e.fjh === fj)
    }
    // @Action({commit: 'setSz'})
    // async getSzFI() {
    //
    // }
}
